.TodoListItem {
    display: flex;
    align-items: center;
    padding: 1rem;

    &:nth-child(even) {
        background-color: #f8f9fa;
    }

    .checkbox {
        display: flex;
        flex: 1;
        align-items: center;
        cursor: pointer;

        svg {
            font-size: 1.5rem;
        }

        .text {
            flex: 1;
            margin-left: 0.5rem;
        }

        &.checked {
            svg {
                color: #22b8cf;
            }

            .text {
                color: #adb5bd;
                text-decoration: line-through;
            }
        }
    }

    .remove {
        display: flex;
        align-items: center;
        font-size: 1.5rem;
        color: #ff6b6b;
        cursor: pointer;
        &:hover {
            color: #ff8787;
        }
    }

    & + & {
        margin-top: 1px solid #dee2e6;
    }
}