.TodoTemplate {
    width: 512px;
    margin: 6rem auto 0;
    border-radius: 4px;
    overflow: hidden;

    .app-title {
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        height: 4rem;
        font-size: 1.5rem;
        background-color: #22b8cf;
    }

    .content {
        background-color: white;
    }
}